import { defineComponent } from "vue";
import { useStore } from "vuex";
import router from "@/router";
import { WNInterface } from "vue-wni";
let store;
export default defineComponent({
    name: "SideMenu",
    data() {
        return {
            appVersionValue: '1.0.0',
            newAppVersionValue: '',
            userName: '',
            userId: ''
        };
    },
    methods: {
        versionBtn() {
            if (this.appVersionValue === this.newAppVersionValue) {
                store.dispatch("UpdatePopUp/updatePopUp", { showModal: true, content: "최신버전입니다." });
                this.wnDialogShow();
            }
            else if (this.newAppVersionValue === 'version-error') {
                store.dispatch("UpdatePopUp/updatePopUp", { showModal: true, content: "버전을 불러올 수 없습니다." });
                this.wnDialogShow();
            }
            else {
                store.dispatch("UpdatePopUp/updatePopUp", { showModal: true, content: "업데이트 가능한 버전이 있습니다." });
                this.wnDialogShow();
            }
        },
        hiddenListBtn() {
            router.push({ name: "HiddenList" });
        },
        alarmBtn() {
            router.push({ name: "Alarm" });
        },
        logoutBtn() {
            store.dispatch("SelectPopUp/selectPopUp", { showModal: true, value: "logout" });
            this.wnDialogShowSelect();
        },
        async wnDialogShow() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: false });
                    }
                })
            });
        },
        async wnDialogShowSelect() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("SelectPopUp/selectPopUp", { showModal: false });
                    }
                })
            });
        },
        //앱 버전 가지고 오기
        async appVersion() {
            await WNInterface.execute("wnAppVersion", {
                callback: WNInterface.cb(async (data) => {
                    this.appVersionStr = data.version;
                })
            });
        },
    },
    async created() {
        store = useStore();
        this.userName = store.getters["UserInfo/getUserName"];
        this.userId = await this.$storage.getItem('userId');
        //앱 버전 가져오기
        await this.appVersion();
        const appVersion = await store.dispatch("AppVersion/appVersion", JSON.stringify({ userId: this.userId }));
        if (appVersion !== "error") {
            this.newAppVersionValue = appVersion.datas[0].appVersionValue;
        }
        else {
            this.newAppVersionValue = "version-error";
        }
    }
});
