    mounted() {
        let menu_bg = document.getElementById('menu_bg1');
        menu_bg.style.display = 'none';
        $(document).ready(function () {
            $('.menu_btn>a').on('click', function () {
            });
            $('.menu_bg').on('click', function () {
                $('.menu_bg').hide();
                $('.sidebar_menu').animate({
                    right: '-' + 75 + '%'
                }, function () {
                    $('.sidebar_menu').hide();
                });
            });
            $('#refresh').on('click', function () {
                $('.menu_bg').hide();
                $('.sidebar_menu').animate({
                    right: '-' + 75 + '%'
                }, function () {
                    $('.sidebar_menu').hide();
                });
            });
        });
    },
    methods: {
        async refreshBtn() {
            //초기화
            this.pushList = [];
            await this.getPushList();
        },
        async getPushList() {
            if (!this.loginToggle) {
                this.loginToggle = true;
                //메인 리스트 가져오기
                const VALUE = await store.dispatch("PushList/pushList", JSON.stringify({ userId: this.userId }));
                this.loginToggle = false;
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    this.pushList = VALUE.datas[0]['appUserPushMessageVO'];
                    if (this.pushList.length == 0) {
                        this.noDataText = "학습 알림이 존재하지 않습니다.";
                    }
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "notToken" && VALUE !== "tokenExpire") {
                        await this.getPushList();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.serverError();
                }
            }
        },
        //서버 에러
        serverError() {
            this.popUpDialog('서버와 연결이 불안정합니다. <br> 잠시 후 다시도해주세요.');
        },
        popUpDialog(content) {
            store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: true, content: content });
            this.wnDialogShow();
        },
        swipe(index) {
            return (direction, event) => {
                console.log(index, direction, event);
                this.removeDate(index);
            };
        },
        async removeDate(index) {
            let pushCd = this.pushList[index]["pushCd"];
            const VALUE = await store.dispatch("PushList/pushHidden", JSON.stringify({ pushCd: pushCd, viewYn: 'N' }));
            if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                this.pushList[index]['viewYn'] = 'N';
                this.removeValue = this.removeValue + 1;
                if (this.removeValue === this.pushList) {
                    this.noDataText = "학습 알림이 존재하지 않습니다.";
                }
            }
            else if (VALUE === 'tokenExpire') {
                const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                if (VALUE !== "error" && VALUE !== "notToken") {
                    await this.removeDate(index);
                }
                else {
                    await this.$router.replace({ name: 'Login' });
                }
            }
            else {
                console.log("에러발생");
            }
        },
        async logoutBtn() {
            //초기화
            //await store.dispatch("UserInfo/removeUserinfo");
            const VALUE = await store.dispatch("Logout/logout", JSON.stringify({ userId: this.userId }));
            if (VALUE !== 'error') {
                await this.$storage.removeItem("accessToken");
                await this.$storage.removeItem("userId");
                await store.dispatch("UserInfo/removeUserinfo");
                await this.$router.replace({ name: 'Login' });
                //로그아웃 시 필요한 거 해주기
                //쿠키 삭제  아이디 비밀번호 초기화 등
            }
            else {
                this.popUpDialog('서버와 연결이 불안정합니다. <br> 잠시 후 다시도해주세요.');
            }
        },
        menu_btn_a() {
            $('.menu_bg').show();
            $('.sidebar_menu').show().animate({
                right: 0
            });
            this.wnDialogShow();
        },
        //보여지는 날짜 설정해주기
        setDate(item, index) {
            //번호가 0번쨰가 아닐 때
            if (index !== 0) {
                let beforeValue = index - 1;
                if (beforeValue !== 0) {
                    if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item) {
                        while (this.pushList[beforeValue]['viewYn'] !== 'Y' && beforeValue > 0) {
                            beforeValue = beforeValue - 1;
                        }
                        if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item && this.pushList[beforeValue]['viewYn'] === 'Y') {
                            return '';
                        }
                    }
                    else {
                        if (this.pushList[index]['viewYn'] === 'Y') {
                            return item;
                        }
                        else {
                            return '';
                        }
                    }
                }
                else {
                    if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item) {
                        return '';
                    }
                    else {
                        if (this.pushList[index]['viewYn'] === 'Y') {
                            return item;
                        }
                        else {
                            return '';
                        }
                    }
                }
            }
            //번호가 0 번째 일때
            else {
                if (this.pushList[index]['viewYn'] === 'Y') {
                    return item;
                }
                else {
                    return '';
                }
            }
        },
        changeDate(dateSet) {
            let year = dateSet.substring(0, 4);
            let month = dateSet.substring(4, 6);
            let date = dateSet.substring(6, 8);
            return year + "-" + month + "-" + date;
        },
        changeDate2(dateSet) {
            let hour = this.convertTime(Number(dateSet.substring(8, 10)));
            let minute = dateSet.substring(10, 12);
            // let second = dateSet.substring(12,14);
            // return hour+"시 "+minute+"분 "+second+"초"
            return hour + "시 " + minute + "분";
        },
        //시간 변경 해주기
        convertTime(number) {
            if (number >= 12) {
                let setNumber = 0;
                if (number != 12) {
                    setNumber = number - 12;
                }
                else {
                    setNumber = number;
                }
                if (setNumber < 10) {
                    return String("오후 " + "0" + setNumber);
                }
                else {
                    return String("오후 " + setNumber);
                }
            }
            else {
                if (number < 10) {
                    return String("오전 " + "0" + number);
                }
                else {
                    return String("오전 " + number);
                }
            }
        },
        async wnDialogShow() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        $('.menu_bg').hide();
                        $('.sidebar_menu').animate({
                            right: '-' + 75 + '%'
                        }, function () {
                            $('.sidebar_menu').hide();
                        });
                    }
                })
            });
        },
    }
