import { defineComponent } from "vue";
import { useStore } from "vuex";
let store;
export default defineComponent({
    name: "UpdatePopup",
    props: {
        content: {
            type: String,
            required: true,
        },
    },
    data() {
        return {};
    },
    created() {
        store = useStore();
    },
    methods: {
        cancelBtn() {
            store.dispatch("UpdatePopUp/updatePopUp", { showModal: false });
        },
        updateBtn() {
            store.dispatch("UpdatePopUp/updatePopUp", { showModal: false });
            //업데이트 진행
            if (/iPhone|iPad|iPod/i.test(navigator.userAgent)) {
                //아이폰
                //alert("아이폰")
            }
            //안드로이드 기기인 경우
            else if (/Android/i.test(navigator.userAgent)) {
                //안드로이드
                //alert("안드로이드")
                //플레이스토어 주소
                var link = 'http://www.naver.com';
                window.open(link);
            }
        }
    },
    computed: {
        setContent() {
            return store.getters["UpdatePopUp/getContent"];
        }
    }
});
